:root {
    --black: #150605;
    --red: #EA453C;
    --pink: #F47B6E;
    --light-blue: #EFFBFE;
    --blue: #5ddeec;
}

header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    padding: 5px 10px;
    -webkit-box-shadow: 0px 2px 10px -5px rgba(21,6,5,1);
    -moz-box-shadow: 0px 2px 10px -5px rgba(21,6,5,1);
    box-shadow: 0px 2px 10px -5px rgba(21,6,5,1);
}

.header {
    text-align: center;
}

.furniture {
    max-width: 1000px;
    display: flex;
    padding: 1em;
    flex-wrap: wrap;
    margin: 0 auto;
    background: beige;
    justify-content: center;
    
}

.card {
    margin: 1em;
    position: relative;
}

.image {
    height: 150px;
    max-height: 200px;
    
}

.favorite {
    color: var(--red);
    font-size: 1.2rem;
    position: absolute;
    top: 5px;
    left: 5px;
    cursor: pointer;
}

.cart {
    color: var(--black);
    font-size: 2rem;
    position: absolute;
    top: 5px;
    right: 5px;
    cursor: pointer;
}

main.cart-page {
    padding: 10px;
}

.cart-page > h1 {
    text-align: center;
}

.cart-page .total-cost {
    text-align: right;
    font-size: 1.3em;
}

.cart-page div.order-button {
    display: flex;
    justify-content: center;
}

div.order-button > button {
    border-color: black;
    cursor: pointer;
    padding: 15px;
}

.cart-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px 0;
}



.cart-item img {
    margin-right: auto;
    margin-left: 10px;   
    width: 150px;
    margin-top: 5px;
}

.cart-left {
    display: flex;
    justify-content: center;
    align-items: center;
}

.cart-item h2 {
    margin-left: 10px;
}

@media (max-width: 450px) {
    /* .cart-item {
        flex-direction: column;
    } */
    .cart-left {
        flex-direction: column;
    }
}

